
import ListCell from '../../crm/components/ListCell.vue'
import { mapState } from 'vuex'
import PlateType from '../../../js/enum.js'
import event from '../../../js/Event'
import { AjaxHttp } from '../../../js/ajaxHttp'
import { getTimestamp, isArrayType, getImageUrl, getHMFormateTime, getYMDFormateTime, getYMDFormateTimestamp } from "../../../js/utility.js"
import { throttle } from '../../../js/utils.js';
import $ from 'jquery'
import Tabbar from 'vux/src/components/tabbar/tabbar.vue'
import TabbarItem from 'vux/src/components/tabbar/tabbar-item.vue'
import Group from 'vux/src/components/group/index.vue'
import Datetime from 'vux/src/components/datetime/index.vue'

import { PLATFORM } from '../../../config.js'

export default {
name: 'teamsign',
components: {
Tabbar,
TabbarItem,
ListCell,
Group,
Datetime
},
computed: {
...mapState([
'detail',
'detailSecond',
'userInfo'
]),
rangeName() {
if (this.detailSecond.hasSelect) {
return this.detailSecond.rangeName;
}
return this.userInfo.department_name;
}
},
beforeRouteLeave(to, from, next) {

if (to.name === 'office') { //离开页面返回上层，需要重置数据
this.$store.commit('updateDetailSecond', '');
} else { //记录所有状态
if (this.detailSecond === '' || this.detailSecond.type !== 'teamsign' || to.name === 'user') {
this.$store.commit('updateDetailSecond', {
today_count: this.today_count,
itemlist: this.itemlist,
navtype: this.navtype,
triangleright: this.triangleright,
type: 'teamsign',
p: this.p,
date: this.date,
totalheight: this.totalheight,
loadDone: this.loadDone,
refresh: false
});
} else {
this.detailSecond.today_count = this.today_count;
this.detailSecond.itemlist = this.itemlist;
this.detailSecond.navtype = this.navtype;
this.detailSecond.triangleright = this.triangleright;
this.detailSecond.type = 'teamsign';
this.detailSecond.p = this.p;
this.detailSecond.date = this.date;
this.detailSecond.totalheight = this.totalheight;
this.detailSecond.loadDone = this.loadDone;
this.detailSecond.refresh = false;
this.$store.commit('updateDetailSecond', this.detailSecond);
}
}
next();
},
data() {
return {
itemlist: [],
today_count: "0",
map: null,
triangleright: "45px",
navtype: 'left',
p: 1,
date: '',
selectDateF: '',
datePickerVisibility: false,
totalheight: 0, //目前整个页面内容的高度
loadDone: false, //无载入完成
};
},
mounted() {
this.triangleright = (window.innerWidth - 50) + 'px';

if (this.detail !== '' && this.detail.type === PlateType.SignIn && this.detail.address !== '') {
var map = new BMap.Map("teammap");
this.map = map;
map.disableDragging(); 
map.enableScrollWheelZoom(false);
map.disableDoubleClickZoom();
map.centerAndZoom(this.detail.address.point, 15);
this.addMarkerLabel(this.detail.address.point);

} else {
var map = new BMap.Map("teammap");
this.map = map;
map.disableDragging(); 
map.enableScrollWheelZoom(false);
map.disableDoubleClickZoom();
var self = this;
this.$vux.loading.show({
text: '位置获取中'
});
if (PLATFORM !== 'web' && this.isAndroid()) { //this.isAndroid()&&navigator.geolocation
function successCallback(data) {
self.$vux.loading.hide();
var point = new BMap.Point(parseFloat(data.longitude), parseFloat(data.latitude));
map.centerAndZoom(point, 15);
self.addMarkerLabel(point);

};

function failedCallback(data) {
self.$vux.loading.hide();

self.$vux.toast.text('定位失败', 'bottom');
};
baidu_location.getCurrentPosition(successCallback, failedCallback);
} else if (PLATFORM !== 'web' && this.isIOS()) {
var onSuccess = function(position) {
self.$vux.loading.hide();
var point = new BMap.Point(parseFloat(position.coords.longitude), parseFloat(position.coords.latitude));
var convertor = new BMap.Convertor();
var pointArr = [];
pointArr.push(point);
convertor.translate(pointArr, 1, 5, function(data) {
if (data.status === 0) {
var newpoint = data.points[0];
map.centerAndZoom(newpoint, 15);
self.addMarkerLabel(newpoint);
} else {
self.$vux.loading.hide();

self.$vux.toast.text('定位失败', 'bottom');
}
});
};


function onError(error) {
self.$vux.loading.hide();

self.$vux.toast.text('定位失败', 'bottom');
}

navigator.geolocation.getCurrentPosition(onSuccess, onError);
} else {
var geolocation = new BMap.Geolocation();
geolocation.getCurrentPosition(function(r) {
self.$vux.loading.hide();
if (this.getStatus() == BMAP_STATUS_SUCCESS) {
map.centerAndZoom(r.point, 15);
self.addMarkerLabel(r.point);
} else {
self.$vux.toast.text('定位失败', 'bottom');
}
}, {
enableHighAccuracy: true,
maximumAge: 0
})
}
}


if (this.detailSecond !== '' && this.detailSecond.type === 'teamsign') {
this.itemlist = this.detailSecond.itemlist;
this.today_count = this.detailSecond.today_count;
this.triangleright = this.detailSecond.triangleright;
this.navtype = this.detailSecond.navtype;
this.p = this.detailSecond.p;
this.date = this.detailSecond.date;
this.totalheight = this.detailSecond.totalheight;
this.loadDone = this.detailSecond.loadDone;
if (this.detailSecond.refresh) {
this.p = 1;
this.loadDone = false;
this.getDetail();
}
} else {
this.date = getTimestamp(new Date());
this.getDetail();
}

this.$nextTick(() => {
this.selectDateF = getYMDFormateTime(this.date);
});

$('#scrollContent').on('scroll', throttle(this.getScrollData, 300, 500));
},
methods: {
isAndroid() {
var u = navigator.userAgent;
return u.indexOf('Android') > -1 || u.indexOf('Adr') > -1;
},
isIOS() {
var u = navigator.userAgent;
return !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/);;
},
onIndexChange(newIndex, oldIndex) {
if (newIndex === 0) {
this.$router.replace({
path: '/fieldsign/default'
})
}
},
addMarkerLabel(point) {
this.map.clearOverlays()
var myIcon = new BMap.Icon(require("../../../assets/sign_loc.png"), new BMap.Size(300, 157));
var marker2 = new BMap.Marker(point, { icon: myIcon }); 
this.map.addOverlay(marker2);;
},
checkHistory() {
this.$router.push({
name: 'signhistory'
})
},
getDetail() {
if (this.loadDone) { return; }

var postParams = {};
if (this.detailSecond !== '' && this.detailSecond.type === 'teamsign') {
postParams = { 'date': this.date, 'p': this.p, 'department_id': this.detailSecond.departmentlist, 'role_id': this.detailSecond.rolelist };
} else {
postParams = { 'date': this.date, 'p': this.p };
}
AjaxHttp('POST', 'm=sign&a=index', postParams, this, function(res, wself, success) {
if (success && isArrayType(res.data.list)) {
wself.today_count = res.data.today_count;
if (wself.p === 1) {
wself.itemlist = res.data.list;
} else {
wself.itemlist = wself.itemlist.concat(res.data.list);
}

if (wself.p === 1 && res.data.list.length < 10) {
wself.loadDone = true;
} else if (res.data.list.length === 0) {
wself.loadDone = true;
} else {

}

wself.p = wself.p + 1;
wself.$nextTick(() => {
var div = document.getElementById('scrollContent');
wself.totalheight = div.scrollHeight;
});


}
});
},
getUserImage(url) {
return getImageUrl(url);
},
getTime(time) {
return getHMFormateTime(time);
},
enterCustomer(customer_id) {
this.$router.push({
name: 'maindetail',
params: {
id: customer_id,
type: PlateType.Customer
}
});
},
enterUser(role_id) {
this.$router.push({
name: 'user',
params: {
id: role_id
}
})
},
getScrollData() {
let totalheight = parseInt($('#scrollContent').height(), 10) + parseInt($('#scrollContent').scrollTop(), 10);
if (this.totalheight === totalheight) {
this.getDetail();
}
},
selectedTime() {
this.datePickerVisibility = true;
























},
datePickerChange(value) {
if (this.selectDateF !== value) {
this.loadDone = false;
this.selectDateF = value;
this.p = 1;
this.today_count = 0;
this.itemlist = [];
this.date = getYMDFormateTimestamp(this.selectDateF);
this.getDetail();

}
},
previewerImg(images, imgindex) {
var previewerlist = [];
for (var i = 0; i < images.length; i++) {
previewerlist.push({
src: getImageUrl(images[i])
});
}
event.$emit('previewerAction', { list: previewerlist, index: imgindex });
},
choiceRange() {

this.$router.push({
name: 'rangechoice',



})





}
}
};




